import { useEffect, useRef } from 'react';

export const AnimationClassName = {
  BOTTOM_TO_TOP: 'bottom-to-top',
};

const Animation = ({ children, className, animationClassName, order = 1 }) => {
  const animationRef = useRef(null);

  const fullClassName = `${className} opacity-hidden ${animationClassName}-off`;

  useEffect(() => {
    const delay = order * 100;

    const observer = new IntersectionObserver(
      (entries) => {
        entries.forEach((entry) => {
          const target = entry.target;

          if (entry.isIntersecting) {
            const transitionDelay = `${delay}ms`; // 
            target.style.transitionDelay = transitionDelay;

            target.classList.add(animationClassName);
            target.classList.remove(`${animationClassName}-off`);
          }
          //   else {
          //     target.classList.add(`${animationClassName}-off`);
          //     target.classList.remove(animationClassName);
          //   }
        });
      },
      {
        root: null,
        threshold: 0.3,
      },
    );

    const { current } = animationRef;
    if (current) {
      observer.observe(current);
    }

    return () => {
      if (current) {
        observer.unobserve(current);
      }
    };
  }, [animationClassName, order]);

  return (
    <div className={fullClassName} ref={animationRef}>
      {children}
    </div>
  );
};

export default Animation;
