import { useEffect, useState } from 'react';
import { FiMenu } from 'react-icons/fi';
import { IoClose } from 'react-icons/io5';

const PCHeader = ({ handleScroll }) => {
  const [isHeaderChange, setIsHeaderChange] = useState(false);

  useEffect(() => {
    const updateScrollPosition = () => {
      if (window.scrollY > 200) {
        setIsHeaderChange(true);
      } else {
        setIsHeaderChange(false);
      }
    };

    window.addEventListener('scroll', updateScrollPosition);

    return () => {
      window.removeEventListener('scroll', updateScrollPosition);
    };
  }, []);

  return (
    <div className="pc-headder">
      <div className="inner">
        <div className="left">
          <p className={`${isHeaderChange ? 'scrolled' : ''}`}>Frameworks</p>
        </div>
        <div className={`center ${isHeaderChange ? 'scrolled' : ''}`}>
          <p>Frameworks</p>
          <div className={`menu ${isHeaderChange ? 'scrolled' : ''}`}>
            <div className="item" onClick={() => handleScroll('section-1')}>
              <p className="p">Product</p>
              <p className="p-hover">Product</p>
            </div>
            <div className="item" onClick={() => handleScroll('section-2')}>
              <p className="p">Pricing</p>
              <p className="p-hover">Pricing</p>
            </div>
            <div className="item" onClick={() => handleScroll('section-3')}>
              <p className="p">Docs</p>
              <p className="p-hover">Docs</p>
            </div>
            <div className="item" onClick={() => handleScroll('section-4')}>
              <p className="p">Blog</p>
              <p className="p-hover">Blog</p>
            </div>
          </div>
        </div>
        <div />
      </div>
    </div>
  );
};

const MobileHeader = ({ handleScroll }) => {
  const [isOpen, setIsOpen] = useState(false);

  const handleToggle = () => {
    setIsOpen((prev) => !prev);
  };

  useEffect(() => {
    if (isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
  }, [isOpen]);

  return (
    <div className="mobile-headder">
      <div className={`top-box ${isOpen ? 'open' : ''}`}>
        <p>Frameworks</p>
        <div className="btn">
          {isOpen ? <IoClose size={28} onClick={handleToggle} /> : <FiMenu size={26} onClick={handleToggle} />}
        </div>
      </div>
      <div className={`menu ${isOpen ? 'open' : ''}`}>
        <ul>
          <li
            onClick={() => {
              handleScroll('section-1');
              setIsOpen(false);
            }}
          >
            Product
          </li>
          <li
            onClick={() => {
              handleScroll('section-2');
              setIsOpen(false);
            }}
          >
            Pricing
          </li>
          <li
            onClick={() => {
              handleScroll('section-3');
              setIsOpen(false);
            }}
          >
            Docs
          </li>
          <li
            onClick={() => {
              handleScroll('section-4');
              setIsOpen(false);
            }}
          >
            Blog
          </li>
        </ul>
      </div>
    </div>
  );
};

const Header = () => {
  const handleScroll = (sectionId) => {
    const section = document.getElementById(sectionId);

    if (section) {
      section.scrollIntoView({ behavior: 'smooth' });
    }
  };

  return (
    <>
      <PCHeader handleScroll={handleScroll} />
      <MobileHeader handleScroll={handleScroll} />
    </>
  );
};

export default Header;
