import { useEffect, useRef, useState } from 'react';
import { IoClose } from 'react-icons/io5';
import { BsArrowUpRightCircleFill } from 'react-icons/bs';

const Chatbot = () => {
  const [isOpen, setIsOpen] = useState(false);
  const chatWrapRef = useRef(null);

  const handleToggle = () => {
    setIsOpen((prev) => !prev);
  };

  const handleClickOutside = (event) => {
    if (chatWrapRef.current && !chatWrapRef.current.contains(event.target)) {
      setIsOpen(false); //    
    }
  };

  useEffect(() => {
    if (window.innerWidth <= 810 && isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'unset';
    }
  }, [isOpen]);

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  return (
    <div ref={chatWrapRef} className="chat-wrap">
      <div className="inner">
        {isOpen ? (
          <img src={require('assets/images/chatbot-close.png')} alt="" onClick={handleToggle} />
        ) : (
          <img src={require('assets/images/chatbot.png')} alt="" onClick={handleToggle} />
        )}
        <div className={`message-box ${isOpen ? 'open' : ''}`}>
          <div className="top">
            <p>Frameworks</p>
            <div className="btn">
              <IoClose size={28} onClick={handleToggle} />
            </div>
          </div>
          <div className="body"></div>
          <div className="bottom">
            <input type="text" />
            <div className="btn">
              <p></p>
              {/* <BsArrowUpRightCircleFill/> */}
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Chatbot;
