import { useEffect } from 'react';
import gsap from 'gsap';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

gsap.registerPlugin(ScrollTrigger);

const useParallaxScroll = (targetRef, animationSetting) => {
  useEffect(() => {
    if (!targetRef.current) return;

    const animation = gsap.fromTo(targetRef.current, animationSetting.from, {
      ...animationSetting.to,
      scrollTrigger: {
        trigger: targetRef.current,
        ...animationSetting.scrollTrigger,
      },
    });

    return () => {
      if (animation.scrollTrigger) {
        animation.scrollTrigger.kill();
      }

      animation.kill();
    };
  }, [targetRef, animationSetting]);
};

export default useParallaxScroll;
